import axios from 'axios';
import {browserHistory} from 'react-router';
import Common from './common';

const CancelToken = axios.CancelToken;
const source = CancelToken.source();
//
axios.interceptors.request.use((config) => {
  const token = localStorage.getItem('token');
  config.headers['access-token'] = token;
  config.loadingEle && Common.setLoading(config.loadingEle);
  return config;
}, (error) => {
  return Promise.reject(error);
})

// response
// eslint-disable-next-line consistent-return
axios.interceptors.response.use((response) => {
  const data = response.data;
  response.config.loadingEle && Common.removeLoading(response.config.loadingEle);
  // code
  if (data.errorCode === 0) {
    // 
    data.description = '';
    localStorage.removeItem('token');
    browserHistory.push('/login');
    return data;
  }
}, (err) => { // 200
  if (err && err.response) {
    switch (err.response.status) {
      case 400:
        err.message = '';
        break;

      case 401:
        err.message = '';
        break;

      case 403:
        err.message = '';
        break;

      case 404:
        err.message = `: ${err.response.config.url}`;
        break;

      case 408:
        err.message = '';
        break;

      case 500:
        err.message = '';
        break;

      case 501:
        err.message = '';
        break;

      case 502:
        err.message = '';
        break;

      case 503:
        err.message = '';
        break;

      case 504:
        err.message = '';
        break;

      case 505:
        err.message = 'HTTP';
        break;

      default:
    }
  }
  err.config.loadingEle && Common.removeLoading(err.config.loadingEle);
  return Promise.reject(err);
})

export const request = (url, param, method = 'get', loadingEle, option = {}) => {
  return axios({
    method: method,
    url: url,
    data: param,
    loadingEle: loadingEle,
    ...option
  })
}




